// NativeInteractiveElements

export default {
	'Image and multimedia': `
		<div>

		</div>
	`,
	'Inputs': `
		<div>
			<form>
				<fieldset>
					<legend>Buttons types</legend>

					<button>button element</button>
					<input type="button" value="Input button">
					<input type="submit" value="Submitee">
					<input type="reset" value="Resetee">

					<input type="image" alt="cat" src="">Input image

					</br>

					<button disabled>button element</button>
					<input type="button" disabled value="Input button">
					<input type="submit" disabled value="Submitee">
					<input type="reset" disabled value="Resetee">

					<input type="image" alt="cat" src="">Input image
				</fieldset>

				<fieldset>
					<legend>Date & time</legend>

					<label>date <input type="date"></label>
					<label>datetime-local <input type="datetime-local"></label>
					<label>month <input type="month"></label>
					<label>week <input type="week"></label>
					<label>time <input type="time"></label>
				</fieldset>

				<fieldset>
					<legend>Multiple choice or range</legend>

					<label>
						number <input type="number" placeholder="placeholder">
					</label>

					<span>
						Radio
						<label>
							<input type="radio" name="radio-sample"><span>radio_1</span>
						</label>
						<label>
							<input type="radio" name="radio-sample"><span>radio_2</span>
						</label>
						<label>
							<input type="radio" name="radio-sample"><span>radio_3</span>
						</label>
						<label>
							<input type="radio" name="radio-sample">radio_x
						</label>
					</span>

					<div>
						<h1>HAHAHAHA</h1>
						<input type="radio" name="radio-sample">
					</div>

					<label>
						<span>checkbox<span>
						<input type="checkbox">
					</label>
					<select>
						select
						<option value="">choose one</option>
						<optgroup label="Standard">
							<option value="dog">dog</option>
							<option value="cat">cat</option>
							<option value="horse">horse</option>
						</optgroup>
						<optgroup label="Sauropods">
							<option value="Diplodocus">Diplodocus</option>
							<option value="Saltasaurus">Saltasaurus</option>
							<option value="Apatosaurus">Apatosaurus</option>
						</optgroup>
					</select>

					<label>
						using data-list 
						<input list="ids-are-evil"/>
						<datalist id="ids-are-evil">
							<option value="dog">
							<option value="cat">
							<option value="horse">
							<option value="Diplodocus">
							<option value="Saltasaurus">
							<option value="Apatosaurus">
						</datalist>
					</label>

					<label>range <input type="range"></label>
				</fieldset>

				<fieldset>
					<legend>Select from somewhere</legend>

					<label>Color <input type="color"></label>
					<label>
						<span>Input file </span>
						<input type="file">
					</label>
				</fieldset>

				<fieldset>
					<legend>Texts</legend>

					<label>email <input type="email" placeholder="email"></label>
					<label>search <input type="search"></label>
					<label>tel <input type="tel"></label>
					<label>text <input type="text"></label>
					<label>text (disabled)<input type="text" disabled></label>
					<label>url<input type="url"></label>
					<label>password<input type="password"></label>

					<label>
						Textarea (10 rows, 50 col)
						<textarea rows="10" cols="50" maxlength="1000">This da textarea... Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Maecenas porttitor congue massa. Fusce posuere, magna sed pulvinar ultricies, purus lectus malesuada libero, sit amet commodo magna eros quis urna. Nunc viverra imperdiet enim. Fusce est. Vivamus a tellus. Pellentesque habitant morbi tristique senectus et netus et malesuada fames ac turpis egestas. Proin pharetra nonummy pede.</textarea>
					</label>
					<label>
						Textarea
						<textarea maxlength="1000">This da textarea... Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Maecenas porttitor congue massa. Fusce posuere, magna sed pulvinar ultricies, purus lectus malesuada libero, sit amet commodo magna eros quis urna. Nunc viverra imperdiet enim. Fusce est. Vivamus a tellus. Pellentesque habitant morbi tristique senectus et netus et malesuada fames ac turpis egestas. Proin pharetra nonummy pede.</textarea>
					</label>
				</fieldset>
			</form>
		</div>
	`,
	'Indicators': `
		<div>
			<fieldset>
				<legend>meter</legend>

				<div>
					Where: <code>min=0, low=33, high=66, max=100; optimum=80;</code>
				</div>
				<label title="min=0, max=100">
					Optimum (value=10):  
					<meter name="fuel" min="0" max="100" low="33" high="66" optimum="20" value="10"></meter>
				</label>
				<br>
				<label>
					Sub-optimum (value=50): 
					<meter name="fuel" min="0" max="100" low="33" high="66" optimum="20" value="50"></meter>
				</label>
				<br>
				<label>
					Sub-sub-optimum (value=90): 
					<meter name="fuel" min="0" max="100" low="33" high="66" optimum="20" value="90"></meter>
				</label>

				<hr>
				<div>
					Where: <code>min=0, max=100</code>
				</div>
				<label>
					<meter name="fuel" min="0" max="100" value="90"></meter>
				</label>
			</fieldset>

			<fieldset>
				<legend>progress</legend>
				<label>
					Determinate:
					<progress min="0" max="100" value="70"> 70% </progress>
				</label>
				<label>
					Indeterminate:
					<progress min="0" max="100"> 70% </progress>
				</label>
			</fieldset>
		</div>
	`,
	'HTML native UI elements': `
		<div>

		</div>
	`
}